import back from '@/components/back'
import { setInterval, clearInterval } from 'timers';

export default {
    components:{
        back
    },
    data(){
        return {
            title: '新起点互联网医院处方笺',
            isEdit: false,
            obj: null,
            ListDetails: null,
            currentdate: '',
            info: this.$store.state.patient,
            mod: false,
            deadline: 1,
            days: 1,
            Diagnoses: '',
            useDays:'',
            ValidDaysRemark: '',
            PrescriptionNo: '',
            MedicationCompanyId: '',
            MedicationCompanyName: '',
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
            preId: null,
            showConfirm: false,
            popIdx: null,
            timer: null
        }
    },
    created(){
        const data = true
        this.$bridge.callhandler('backConfirm', data)
        this.timer = setInterval(() => {
            this.$bridge.registerhandler('confirmBack', (data, responseCallback) => {
                responseCallback('cantBack')
                this.$refs.back.back()
            })
        },50)
        this.getDate()
        this.createPre()
        const {  upList, Diagnoses2, saveFlag, pullFlag } = this.$store.state
        if(pullFlag == 0){
            this.getMPre()
        }
        this.ListDetails = upList
        this.Diagnoses = Diagnoses2
    },
    beforeDestroy(){
        clearInterval(this.timer)
        this.$store.dispatch('pushDiagnoses2', this.Diagnoses)
    },
    methods:{
        operate(type, i, idx){
            if(type === 'del' && i.Quantity > 1){
                i.Quantity -= 1
            }
            else if(type === 'add'){
                i.Quantity = +i.Quantity + 1
            }
            this.ListDetails.splice(idx, 1, i)
        },
        getDate(){
            var date = new Date();
            var seperator1 = "-";
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            this.currentdate = year + seperator1 + month + seperator1 + strDate;
        },
        createPre(){
            const o = this.$store.state.preNo
            if(o){
                this.PrescriptionNo = o.PrescriptionNo
                this.MedicationCompanyId = o.MedicationCompanyId
                this.MedicationCompanyName = o.MedicationCompanyName
            }
            else{
                this.$post('Prescription/Prescription/GetNewPrescriptionBaseInfo',this.$pick({
                    ApplyId: this.orderInfo?this.orderInfo.Id : 0,
                    DoctorId: this.user.Data.User.DoctorId,
                    OrgId: -1,
                })).then(o => {
                    if(o.IsExistPrescription == 1){
                        this.ListDetails = o.PrescriptionInfo.ListDetails
                        this.Diagnoses = o.PrescriptionInfo.Diagnoses
                        this.deadline = o.PrescriptionInfo.ValidDays
                        this.ValidDaysRemark = o.PrescriptionInfo.ValidDaysRemark
                        this.days = o.PrescriptionInfo.DosageDays
                        this.useDays = o.PrescriptionInfo.DosageDaysRemark
                        this.PrescriptionNo = o.PrescriptionInfo.No
                        this.MedicationCompanyId = o.PrescriptionInfo.MedicationCompanyId
                        this.MedicationCompanyName = o.PrescriptionInfo.MedicationCompanyName
                        this.preId = o.PrescriptionInfo.Id
                        this.$store.dispatch('pushpreNo',{
                            PrescriptionNo: o.PrescriptionInfo.No,
                            MedicationCompanyId: o.PrescriptionInfo.MedicationCompanyId,
                            MedicationCompanyName: o.PrescriptionInfo.MedicationCompanyName
                        })
                    }
                    else{
                        this.$store.dispatch('pushpreNo',o.InitInfo)
                        this.PrescriptionNo = o.InitInfo.PrescriptionNo
                        this.MedicationCompanyId = o.InitInfo.MedicationCompanyId
                        this.MedicationCompanyName = o.InitInfo.MedicationCompanyName
                    }
                })
            }
        },
        pop(idx){
            this.showConfirm = true
            this.popIdx = idx
        },
        confirm(){
            this.ListDetails.splice(this.popIdx, 1)
        },
        modify(idx){
            this.$store.dispatch('pushcMed', this.ListDetails[idx])
            this.$router.push(`/modifyCon?idx=${idx}&&back=-1`)
        },
        getMPre(){
            if(this.$route.query.MPrescriptionId && this.$route.query.MPrescriptionId != 0){
                this.$post('Prescription/Prescription/GetPrescription',{
                    Id: this.$route.query.MPrescriptionId
                }).then(o => {
                    this.$store.dispatch('pushupList', o.ListDetails)
                    this.ListDetails = o.ListDetails
                    this.Diagnoses = o.Diagnoses
                    this.$store.dispatch('pushpullFlag', 1)
                })
            }
            if(this.$route.query.rejectId && this.$route.query.rejectId != 0){
                this.$post('Prescription/Prescription/GetPrescription',{
                    Id: this.$route.query.rejectId
                }).then(o => {
                    this.$store.dispatch('pushupList', o.ListDetails)
                    this.ListDetails = o.ListDetails
                    this.Diagnoses = o.Diagnoses
                    this.$store.dispatch('pushpullFlag', 1)
                })
            }
        },
        send(){
            if(!(this.ListDetails && this.ListDetails.length)){
                return this.$toast('请添加药物')
            }
            if(!this.Diagnoses){
                return this.$toast('请填写临床诊断')
            }
            if(!this.deadline){
                return this.$toast('请填写处方笺有效期')
            }
            const List = []
            this.ListDetails.forEach(v => {
                List.push({
                    MedicationId: v.MedicationId || v.Id,
                    UsageTimeId: v.UsageTimeId,
                    UsageTime: v.UsageTime,
                    UsageMethodId: v.UsageMethodId,
                    UsageMethod: v.UsageMethod,
                    DayDosageId: v.DayDosageId,
                    DayDosage: v.DayDosage,
                    Dosage: v.Dosage,
                    DosageUnitId: v.DosageUnitId,
                    DosageUnit: v.DosageUnit,
                    PackageUnitId: v.PackageUnitId,
                    PackageUnit: v.PackageUnit,
                    Quantity: v.Quantity,
                    Remark: v.Remark,
                    DosageDaysId: v.DosageDaysId,
                    DosageDays: v.DosageDays
                })
            })
            const ApplyId = this.orderInfo && this.orderInfo.Method ? this.orderInfo.Id : null
            const MPrescriptionId = this.orderInfo && this.orderInfo.PrescriptionId ? this.orderInfo.PrescriptionId : this.$route.query.MPrescriptionId
            this.$post('Prescription/Prescription/SavePrescription',this.$pick({
                Id: this.preId,
                MPrescriptionId,
                ApplyId,
                PatientId: this.orderInfo.PatientInfo.PatientId,
                PatientName: this.orderInfo.PatientInfo.PatientName,
                ApplicantId: this.orderInfo.ApplicantId,
                ApplicantName: this.orderInfo.ApplicantName,
                OrgId: 0,
                DoctorId: this.user.Data.User.DoctorId,
                DoctorName: this.user.Data.User.DoctorName,
                Diagnoses: this.Diagnoses,
                ValidDays: this.deadline,
                ValidDaysRemark: this.ValidDaysRemark,
                DosageDays: this.days,
                DosageDaysRemark: this.useDays,
                Remark: '',
                MedicationCompanyId: this.MedicationCompanyId,
                ListDetails: List,
                No: this.PrescriptionNo,
            })).then(o => {
                this.$bridge.callhandler('signId', o.UniqueId, (data) => {
                    this.$router.replace(`/success?name=${this.orderInfo.PatientInfo.PatientName}&NO=${this.PrescriptionNo}`)
                })
            })
        }
    }
}



